.About_div{
	


	grid-column: container-start / container-end;
	display: flex;
	flex-direction: column;
    


	
}

.About_heading{

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
	padding-top: 15rem;
	 padding-left: 15rem;
    padding-right:15rem;
}

.About_heading h2{

	font-size: 3rem;
	color:#326281;
	font-family:  'garamond-premier-pro-display', Sans-serif;
	
}

.About_heading h1{

 padding-top: 2rem;
	font-size: 5rem;
	line-height: 5rem;
	color:#326281;
	
font-family:  'garamond-premier-pro-display', Sans-serif;
}
.About_desc{

 display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: left;
    margin: auto;
    padding-left: 15rem;
    padding-right:15rem;
	padding-top: 5rem;

}

.About_desc p {

	font-size: 2.5rem;
}

.About_desc img {
        padding-right:1rem;
	}
@media screen and (max-width: 900px)  {
.About_desc{

     flex-direction: column;
     padding-left: 3rem;
    padding-right:3rem;
    text-align: justify;
}


}