.panel{

	grid-column: container-start / container-end;
	height: 100vh;
	display: grid;
	grid-template-columns: 50% 50%;
	

	
}
.description{


	display: flex;
   align-items: center;
   justify-content: center;
   background:white;
   color: black;
}
.panelleft{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;

	
}

.panelright{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;

}

.panel h2
{

	font-size: 6rem;
	text-align: center;
   font-family: 'Fondamento', cursive;
}

.panel p{


	padding-top: 3rem;
	font-size: 5rem;
	text-align: center;

}
.panel h3{
         font-size:12rem;  
	  text-decoration: line-through;
	  text-align: center;

}
 .red{
 	color: white;
	background: rgb(20,20,20);
background: linear-gradient(90deg, rgba(20,20,20,1) 0%, rgba(21,23,24,1) 60%, rgba(35,59,62,1) 100%);
}

 .orange{
	background: rgb(90,77,253);
background: linear-gradient(90deg, rgba(90,77,253,1) 0%, rgba(90,77,253,1) 60%, rgba(70,173,251,1) 100%);
}

.purple{
      color: white;
	background: rgb(20,20,20);
background: linear-gradient(90deg, rgba(20,20,20,1) 0%, rgba(21,23,24,1) 60%, rgba(35,59,62,1) 100%);
}
.green{
	background:green;

}
.grey{
	background: rgb(90,77,253);
background: linear-gradient(90deg, rgba(90,77,253,1) 0%, rgba(90,77,253,1) 60%, rgba(70,173,251,1) 100%);
}


@media screen and (max-width: 768px)  {
.panel{

	
	display: grid;
	grid-template-columns: 100%;
	

	
}


.panel p,h2
{

	font-size: 5rem;
	text-align: center;

}

.panel h3{
         font-size:6rem;  
	  text-decoration: line-through;
}
}